import React from "react";
import { Container, Row, Col } from "react-bootstrap";
const About = () => {
	return (
		<section className="section" /* bg-light" */ id="about">
			<Container>
				<Row className="justify-content-center">
					<Col lg={6} md={8}>
						<div className="title text-center mb-5">
							<h3 className="font-weight-normal text-dark">
								Sobre{" "}
								<span className="text-warning">Nosotros</span>
							</h3>

							<p className="text-muted">
								Somos un grupo de amigos de la Universidad
								Tecnológica Nacional, que decimos hacer de
								Syndeo nuestro proyecto final de grado.
							</p>
						</div>
					</Col>
				</Row>
				<Row>
					<Col md={4}>
						<h2 className="font-weight-light line-height-1_6 text-dark mb-4">
							Syndeo, donde disfrutas de la lectura
						</h2>
					</Col>
					<Col md={{ size: 7, offset: 1 }}>
						<Row>
							<Col md={6}>
								<h6 className="text-dark font-weight-light f-20 mb-3">
									Nuestra Misión
								</h6>
								<p className="text-muted font-weight-light text-justify">
									Brindamos las herramientas necesarias para
									potenciar la capacidad de aprendizaje de
									nuestros clientas. Y lo hacemos de manera
									rápida y divertida.
								</p>
							</Col>
							<Col md={6}>
								<h6 className="text-dark font-weight-light f-20 mb-3">
									Nuestra Visión
								</h6>
								<p className="text-muted font-weight-light text-justify">
									Hacer de Syndeo la herramienta principal
									entre los estudiantes de idiomas que deseen
									aprender los mismos mediante el goce de la
									lectura.
								</p>
							</Col>
						</Row>
					</Col>
				</Row>
			</Container>
		</section>
	);
};
export default About;
